import React, { createContext, useContext, useState } from "react";
import { useDispatch } from "react-redux";
import {
  FetchUsers,
  GetPackages,
  GetFrames,
  GetUserDetail,
  GetUserTransaction,
  GetVideos,
} from "../ReduxProvider/CounterSlice";
import axios from "axios";

export const AdminContext = createContext();
export const AdminState = ({ children }) => {
  const [activeTag, setactiveTag] = useState("");
  // const [pack, setPack] = useState();
  const [dashboard, setDashboard] = useState(true);
  const [loader, setLoader] = useState(false);
  const [alert1, setAlert1] = useState("");
  const [pageNo, setPageNo] = useState({
    documents: 1,
    page: 0,
    perPage: 5,
  });
  const [refer, setRefer] = useState({
    referamt: 0,
    _id: "",
  });
  const [condition, setCondition] = useState({ status: "", plan: "" });
  const [active, setActive] = useState({ active: "", Inactive: "" });
  // const host = "http://205e-122-160-47-222.ngrok.io";
  const host = "http://www.adsgrocy.com";
  const dispatch = useDispatch();

  // FetchPackages
  const fetchPackages = async () => {
    try {
      setLoader(true);
      const response = await fetch(`${host}/admindata/getpackagesbyadmin`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
      });
      if (response) {
        const json = await response.json();
        dispatch(GetPackages(json.packagess));
      }
      setLoader(false);
    } catch (error) {
      setLoader(false);
      console.log(error.message);
    }
  };

  // FetchUsers
  const fetchAllUsers = async () => {
    try {
      setLoader(true);
      // console.log(PageNo)
      let temp = JSON.stringify({
        condition,
      });
      const response = await fetch(`${host}/admindata/allUserss`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
        body: temp,
      });
      const json = await response.json();
      // console.log(json)
      if (json) {
        // setLoader(false);
        if (json.length !== pageNo.documents) {
          setPageNo({ ...pageNo, ["documents"]: json.length });
        }
        // console.log(json);
        setLoader(false);
        dispatch(FetchUsers(json));
      }
    } catch (error) {
      setLoader(false);
      console.log(error.message);
    }
  };

  //   CREATE PACKAGE
  const createDynamicPackage = async (
    closeAddPackage,
    plan,
    dailyAds,
    commission,
    expireIn,
    totalROI,
    setAddPackage
  ) => {
    try {
      closeAddPackage.current.click();
      setLoader(true);
      let temp = JSON.stringify({
        plan,
        dailyAds,
        commission,
        expireIn,
        totalROI,
      });

      const response = await fetch(`${host}/admindata/createpackage`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
        body: temp,
      });
      const json = await response.json();

      if (json.message) {
        setAddPackage({
          plan: "",
          dailyAds: "",
          commission: "",
          expireIn: "",
          totalROI: "",
        })
        // setLoader(false)
        fetchPackages();
        // closeAddPackage.current.click();
      } else {
        setLoader(false);
        console.log(json, "res");
      }
      // setLoader(false);
    } catch (error) {
      setLoader(false);
      console.log(error.message);
    }
  };

  //   UPDATE PACKAGE
  const updateDynamicPackage = async (closeP, ids, commission) => {
    try {
      closeP.current.click();
      setLoader(true);
      let temp = JSON.stringify({ commission });
      const response = await fetch(`${host}/admindata/package/update/${ids}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
        body: temp,
      });
      const json = await response.json();
      if (json.message) {
        setLoader(false);
        fetchPackages();
      } else {
        setLoader(false);
        console.log(json, "res");
      }
    } catch (error) {
      setLoader(false);
      console.log(error.message, "err");
    }
  };

  //   DELETE PACKAGE
  const deletePackage = async (id) => {
    try {
      setLoader(true);
      const response = await fetch(`${host}/admindata/package/delete/${id}`, {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
      });
      const json = await response.json();
      if (json.message) {
        setLoader(false);
        fetchPackages();
      } else {
        setLoader(false);
        console.log(json, "res");
      }
    } catch (error) {
      setLoader(false);
      console.log(error.message, "err");
    }
  };

  //   EDIT USER
  const updateUserComm = async (closeUM, id, commission) => {
    try {
      setLoader(true);
      let temp = JSON.stringify({ commission });
      const response = await fetch(`${host}/admindata/user/update/${id}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
        body: temp,
      });
      const json = await response.json();
      if (json.message) {
        setLoader(false);
        closeUM.current.click();
        fetchAllUsers();
      } else {
        setLoader(false);
        console.log(json, "res");
      }
    } catch (error) {
      setLoader(false);
      console.log(error.message, "err");
    }
  };

  // Fetch Banner
  const fetchBanners = async () => {
    try {
      setLoader(true);
      const response = await fetch(`${host}/admindata/banners`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
      });
      const json = await response.json();
      if (json.images) {
        // setLoader(false);
        dispatch(GetFrames(json.images));
      }
      setLoader(false);
    } catch (error) {
      setLoader(false);
      console.log(error.message);
    }
  };

  //   CREATE Banner
  const addBanner = async (imgs, closeBnRef, setImgs) => {
    let formData = new FormData();
    for (let i = 0; i < imgs.length; i++) {
      formData.append("img", imgs[i]);
    }
    try {
      closeBnRef.current.click();
      setLoader(true);
      const response = await fetch(`${host}/admindata/banner`, {
        method: "POST",
        headers: {
          "auth-token": localStorage.getItem("token"),
        },
        body: formData,
      });
      const json = await response.json();
      if (json.message) {
        setLoader(false);
        console.log(json.message);
        fetchBanners();
      } else {
        setLoader(false);
        console.log(json, "res");
      }
      setImgs("");
    } catch (error) {
      setImgs("");
      setLoader(false);
      console.log(error.message);
    }
  };

  // DELETE BANNER
  const bannerDelete = async (id, file) => {
    console.log(file);
    console.log("enter");
    let temp = JSON.stringify({ file });
    try {
      setLoader(true);
      const response = await fetch(`${host}/admindata/banner/delete/${id}`, {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
        body: temp,
      });
      const json = await response.json();
      if (json.message) {
        setLoader(false);
        fetchBanners();
      } else {
        console.log(json, "res");
      }
    } catch (error) {
      setLoader(false);
      console.log(error.message, "err");
    }
  };

  // Fetch Banner
  const fetchAllVideos = async () => {
    try {
      setLoader(true);
      const response = await fetch(`${host}/admindata/AdminVideo`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
      });
      const json = await response.json();
      if (json.videos) {
        dispatch(GetVideos(json.videos));
      }
      setLoader(false);
    } catch (error) {
      setLoader(false);
      console.log(error.message);
    }
  };

  //   CREATE Video
  const addVideos = async (video, closeVideoRef, setImgs) => {
    let formData = new FormData();
    for (let i = 0; i < video.length; i++) {
      formData.append("video", video[i]);
    }
    try {
      closeVideoRef.current.click();
      setLoader(true);
      const response = await fetch(`${host}/admindata/AdminVideo/`, {
        method: "POST",
        headers: {
          "auth-token": localStorage.getItem("token"),
        },
        body: formData,
      });
      const json = await response.json();
      if (json.message) {
        setLoader(false);
        console.log(json.message);
        fetchAllVideos();
      } else {
        console.log(json, "res");
      }
      setImgs("");
    } catch (error) {
      setImgs("");
      setLoader(false);
      console.log(error.message);
    }
  };

  // DELETE BANNER
  const deleteVideos = async (id, file) => {
    console.log(file);
    console.log("enter");
    let temp = JSON.stringify({ file });
    try {
      setLoader(true);
      const response = await fetch(`${host}/admindata/AdminVideo/${id}`, {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
        body: temp,
      });
      const json = await response.json();
      if (json.message) {
        setLoader(false);
        fetchAllVideos();
      } else {
        setLoader(false);
        console.log(json, "res");
      }
    } catch (error) {
      console.log(error.message, "err");
    }
  };

  const Login = async (email, password) => {
    try {
      setLoader(true);
      const response = await fetch(`${host}/auth/loginadmin`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ email: email, password: password }),
      });
      // await json.success===true?{setAlert.type(primary),}
      let json = {};
      if (response) {
        json = await response.json();
      }
      if (json.success) {
        setLoader(false);
        localStorage.setItem("token", json.authtoken);
        setDashboard(false);
        fetchAllUsers();
      } else {
        setLoader(false);
        setAlert1({
          severity: "warning",
          message: `Please Login with Correct Credential`,
        });
        // alert("Invalid Credential");
      }
    } catch (error) {
      setLoader(false);
      setAlert1({
        severity: "warning",
        message: `Please Login with Correct Credential`,
      });
      console.log(error);
    }
  };

  const notificationApi = async (closeM, id, notification, setNotification) => {
    try {
      setLoader(true);
      let temp = JSON.stringify(notification);
      const response = await fetch(`${host}/admindata/notification/${id}`, {
        method: "post",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
        body: temp,
      });
      const json = await response.json();
      if (json.notification) {
        setLoader(false);
        closeM.current.click();
        setNotification({ title: "", text: "" });
      } else {
        setLoader(false);
        console.log(json, "res");
        alert("error");
      }
    } catch (error) {
      console.log(error.message, "err");
    }
  };

  const notificationToAll = async (
    closeAM,
    notification_To_All,
    setNotification_To_All
  ) => {
    try {
      setLoader(true);
      let temp = JSON.stringify(notification_To_All);
      const response = await fetch(`${host}/admindata/notificationtoall`, {
        method: "post",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
        body: temp,
      });
      const json = await response.json();
      if (json.notification) {
        setLoader(false);
        closeAM.current.click();
        setNotification_To_All({ title: "", text: "" });
      } else {
        setLoader(false);
        console.log(json, "res");
        alert("error");
      }
    } catch (error) {
      console.log(error.message, "err");
    }
  };
  const fetchrefer = async () => {
    try {
      setLoader(true);
      const response = await fetch(`${host}/admindata/fetchReferAmount`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
      });
      const json = await response.json();
      if (json) {
        setLoader(false);
        setRefer({
          referamt: json.referamt,
          _id: json._id,
        });
      }
    } catch (error) {
      setLoader(false);
      console.log(error.message);
    }
  };

  const validator = async () => {
    try {
      // setLoader(true);
      if (localStorage.getItem("token")) {
        // let temp = JSON.stringify("notification_To_All");
        const response = await axios.post(
          `${host}/admindata/validator`,
          {},
          {
            headers: {
              "auth-token": localStorage.getItem("token"), //the token is a variable which holds the token
            },
          }
        );
        if (response.data.AUsers) {
          let AUsers = response.data.AUsers;
          setLoader(false);
          setDashboard(false);
          const filterList = AUsers?.filter((x) => {
            if (x.status.toLowerCase().includes("inactive")) return x;
          });
          let active = AUsers.length - filterList.length;
          let inActive = filterList.length;
          console.log(">>>>>>active>>>>>>", active, inActive, filterList);
          setActive({
            ...active,
            ["active"]: active,
            ["InActive"]: inActive,
          });
          fetchAllUsers();
          // fetchPackages();
          // fetchrefer();
        } else {
          // alert("Invalid Credential Please Log again");
          setLoader(false);
          setDashboard(true);
          return;
        }
      } else {
        setLoader(false);
        setDashboard(true);
        return;
      }
    } catch (error) {
      setDashboard(true);
      console.log(error);
      return;
    }
  };

  const editrefer = async (editRefer, closeR) => {
    try {
      setLoader(true);
      let temp = JSON.stringify(editRefer);
      const response = await fetch(
        `${host}/admindata/editReferAmount/${refer._id}`,
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
            "auth-token": localStorage.getItem("token"),
          },
          body: temp,
        }
      );
      const json = await response.json();
      if (json.refer) {
        setLoader(false);
        closeR.current.click();
        setRefer({
          referamt: json.refer,
          _id: json._id,
        });
      } else {
        console.log(json, "res");
      }
    } catch (error) {
      setLoader(false);
      console.log(error.message, "err");
    }
  };
  const UserDetail = async (id) => {
    try {
      setLoader(true);
      // console.log(PageNo)
      const response = await fetch(`${host}/admindata/userDetail/${id}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("token"),
        },
      });
      const json = await response.json();
      if (json.Users) {
        setLoader(false);
        console.log(json);
        dispatch(GetUserDetail([json.Users]));
        if (json.Transaction != "User don't have any transaction yet")
          dispatch(GetUserTransaction(json.Transaction));
        else dispatch(GetUserTransaction(json.Transaction));
      }
    } catch (error) {
      setLoader(false);
      console.log(error.message);
    }
  };

  return (
    <AdminContext.Provider
      value={{
        dashboard,
        setDashboard,
        fetchPackages,
        fetchAllUsers,
        createDynamicPackage,
        updateDynamicPackage,
        deletePackage,
        updateUserComm,
        addBanner,
        fetchBanners,
        bannerDelete,
        Login,
        notificationApi,
        validator,
        pageNo,
        setPageNo,
        condition,
        setCondition,
        notificationToAll,
        active,
        fetchrefer,
        refer,
        editrefer,
        UserDetail,
        addVideos,
        fetchAllVideos,
        deleteVideos,
        setLoader,
        setAlert1,
        loader,
        activeTag,
        setactiveTag,
        host,
        alert1,
      }}
    >
      {children}
    </AdminContext.Provider>
  );
};

const useAdminContexts = () => useContext(AdminContext);

export default useAdminContexts;
