import { createSlice } from "@reduxjs/toolkit";


export const counterSlice = createSlice({
  name: "counter",
  initialState: {
    allPackages: [],
    allUsers: [],
    fetchBanners: [],
    UserDetails: [],
    UserTransactions: [],
    fetchVideos: [],
  },

  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {

    FetchUsers: (state, action) => {
      state.allUsers = action.payload
      // console.log("in counterSlice allUsers" , state.allUsers)
    },

    GetPackages: (state, action) => {
      state.allPackages = action.payload
      // console.log("in counterSlice allPackages" , state.allPackages)
    },

    GetFrames: (state, action) => {
      state.fetchBanners = action.payload
      // console.log("in counterSlice allPackages" , state.allPackages)
    },
    GetUserDetail: (state, action) => {
      state.UserDetails = action.payload
      // console.log("in counterSlice allPackages" , state.allPackages)
    },
    GetUserTransaction: (state, action) => {
      state.UserTransactions = action.payload
      // console.log("in counterSlice allPackages" , state.allPackages)
    },
    GetVideos: (state, action) => {
      state.fetchVideos = action.payload
      // console.log("in counterSlice allPackages" , state.allPackages)
    },

    // // Use the PayloadAction type to declare the contents of `action.payload`
    // incrementByAmount: (state, action) => {
    //   state.value += action.payload;
    // },
  },

});

export const { FetchUsers, GetPackages, GetFrames,GetUserDetail,GetUserTransaction,GetVideos } = counterSlice.actions;

export const packagess = (state) => state.counter.allPackages;

export const allUserss = (state) => state.counter.allUsers;

export const fetchBaners = (state) => state.counter.fetchBanners;
export const UserDetailss = (state) => state.counter.UserDetails;
export const UserTransactionss = (state) => state.counter.UserTransactions;
export const fetchVideoss = (state) => state.counter.fetchVideos;

export default counterSlice.reducer;
