import React from "react";
import styled from "styled-components";
// import useAdminContexts from "../../Context/AdminContext";
// import { Pagination } from "";

const PaginationComponent = ({ pageNo, handlePage,disabled }) => {
  return (
    <Pagination>
      <button
        type="button"
        className="btn btn-primary"
        disabled={pageNo=== 0}
        onClick={(e) => handlePage(e, -1)}
      >
        prev
      </button>
      <p>Page-{pageNo + 1}</p>
      <button
        type="button"
        className="btn btn-primary"
        disabled={disabled}
        onClick={(e) => handlePage(e, +1)}
      >
        next
      </button>
    </Pagination>
  );
};

export default PaginationComponent;

const Pagination = styled.div`
  margin: 10px 1%;
  padding-bottom: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  button {
    border-radius: 40%;
    border: none;
    color: black;
  }
  p {
    margin: 0;
  }

  /* border-radius: ; */
`;