import React, { useEffect, useRef, useState } from "react";
import {
  allUserss,
  packagess,
  UserDetailss,
  GetUserDetail,
  UserTransactionss,
  GetUserTransaction,
} from "../../ReduxProvider/CounterSlice";
import { useDispatch, useSelector } from "react-redux";
import useAdminContexts from "../../Context/AdminContext";
import styled from "styled-components";
import moment from "moment";
import Loader from "../partials/Loader";
import PaginationComponent from "../partials/PaginationComponent";

// import { AllUsers, FetchUsers } from "../../ReduxProvider/CounterSlice";
// import UserDataTable from "./UserDataTable";

const UserData = () => {
  const [search, setSearch] = useState("");
  const ForEditCommission = useRef(null);
  const sendNotification = useRef(null);
  const closeUM = useRef(null);
  const closeR = useRef(null);
  const closeM = useRef(null);
  const clickU = useRef(null);
  const sendNotificationToALl = useRef(null);
  const closeAM = useRef(null);
  const clickN = useRef(null);
  const clickTN = useRef(null);
  const clickDP = useRef(null);
  const {
    updateUserComm,
    notificationApi,
    pageNo,
    setPageNo,
    condition,
    setCondition,
    fetchAllUsers,
    notificationToAll,
    refer,
    editrefer,
    UserDetail,
    loader,
    fetchPackages,
    fetchrefer,
  } = useAdminContexts();
  const dispatch = useDispatch();

  const onChangeNotification_To_All = (e) => {
    // console.log(uId);
    setNotification_To_All({
      ...notification_To_All,
      [e.target.name]: e.target.value,
    });
  };

  // const [filerArray, setFilerArray] = useState([])

  const users = useSelector(allUserss);
  const packages = useSelector(packagess);
  const UserDetails = useSelector(UserDetailss);
  const UserTransactions = useSelector(UserTransactionss);

  //  const temp = useSelector(selectTemp)
  const [editUserComm, setEditUserComm] = useState({ commission: "" });
  const [editRefer, setEditRefer] = useState({ refer: "" });
  const [notification, setNotification] = useState({ title: "", text: "" });
  const [notification_To_All, setNotification_To_All] = useState({
    title: "",
    text: "",
  });
  const [toggleTransaction, setToggleTransaction] = useState(false);
  // console.log("IN user data: ", users)
  const [uId, setUId] = useState({ id: "" });

  const onChangeUserComm = (e) => {
    // console.log(uId);
    setEditUserComm({ ...editUserComm, [e.target.name]: e.target.value });
  };

  const onChangeNotification = (e) => {
    // console.log(uId);
    setNotification({ ...notification, [e.target.name]: e.target.value });
  };

  const updateCommission = (_id) => {
    ForEditCommission.current.click();
    setUId({ ["id"]: _id });
  };

  const OnstatusCodition = async (e) => {
    let value = await e.target.value;
    if (value === "Select Status") {
      setCondition({ ...condition, ["status"]: "" });
    } else {
      setCondition({ ...condition, ["status"]: value });
    }
  };

  const sendNotifications = (_id) => {
    sendNotification.current.click();
    setUId({ ["id"]: _id });
  };

  const OnplanCodition = async (e) => {
    let a = e.target.value;
    // let n = a || "";
    setCondition({ ...condition, ["plan"]: a });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (editUserComm.commission.length > 0) {
      clickU.current.click();
    }
  };

  const handleSubmitForNotification = (e) => {
    e.preventDefault();
    if (notification.text.length > 0) {
      clickN.current.click();
    }
  };

  const handleSubmitNotification_To_All = (e) => {
    e.preventDefault();
    if (notification_To_All.text.length > 0) {
      clickTN.current.click();
    }
  };

  // const handlePage = (e, n) => {
  //   e.preventDefault();
  //   setPageNo({ ...pageNo, ["page"]: pageNo.page + n });
  // };

  const referSubmit = (e) => {
    e.preventDefault();
    editrefer(editRefer, closeR);
  };

  const onChangeRefer = (e) => {
    // console.log(uId);
    setEditRefer({ ...editRefer, [e.target.name]: e.target.value });
  };

  // HANDLE PAGINATION
  const handlePage = (e, n) => {
    e.preventDefault();
    e.target.name === "limit"
      ? setPageNo({
          ...pageNo,
          [e.target.name]: e.target.value < 1 ? 1 : e.target.value,
          ["page"]: 0,
        })
      : setPageNo({
          ...pageNo,
          ["page"]: pageNo.page + n,
        });
  };

  // FILTER LIST FUNCTION
  const filterList = users?.filter((x) => {
    if (search === "") return x;
    else if (
      x.mobile.includes(search.toLowerCase()) ||
      x.name.toLowerCase().includes(search.toLowerCase())
    )
      return x;
  });

  // const DateFilter = filterList.sort((a, b) => b.date - a.date)

  console.log(search);

  useEffect(() => {
    fetchAllUsers();
    if (packages.length <= 0) {
      fetchPackages();
    }
    if (refer.referamt===0) {
      fetchrefer();
    }
  }, [condition]);
  // const cc = users.find(users.status == "active");
  // console.log(UserTransactions);

  return (
    <>
      {toggleTransaction === true ? (
        <>
          <div className="content">
            <div className="container-fluid">
              <div className="row">
                <div className="col-md-12">
                  <div className="card">
                    <Header className="header">
                      <h4 className="title">User Transaction</h4>
                      <button
                        type="button"
                        className="btn btn-primary b-0"
                        onClick={() => {
                          setToggleTransaction(false);
                          dispatch(GetUserTransaction([]));
                        }}
                      >
                        x
                      </button>
                    </Header>
                    <div
                      className={
                        UserTransactions !== "object"
                          ? "UserTransactions"
                          : "content table-responsive table-full-width p-5"
                      }
                    >
                      {typeof UserTransactions === "object" ? (
                        <table className="table">
                          <thead>
                            <tr>
                              <th className="text-center">No.</th>
                              <th className="text-center">Date</th>
                              <th className="text-center">Description</th>
                              <th className="text-center">Amount</th>
                            </tr>
                          </thead>

                          <tbody>
                            {UserTransactions.map((x, index) => {
                              return (
                                <tr key={index}>
                                  <td className="text-center">{index + 1}</td>
                                  {/* 2021-11-24T06:50:08.974Z */}
                                  <td className="text-center">
                                    {x.date.slice(0, 10)}
                                  </td>
                                  <td className="text-center">{x.remark}</td>
                                  <td className="text-center">{x.amount}</td>
                                </tr>
                              );
                            })}
                          </tbody>
                        </table>
                      ) : (
                        <h5>
                          {typeof UserTransactions === "string"
                            ? UserTransactions
                            : "...Loding"}
                        </h5>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </>
      ) : (
        <>
          {/* Modal Button For Commission*/}
          <button
            ref={ForEditCommission}
            type="button"
            className="btn btn-primary d-none"
            data-toggle="modal"
            data-target="#editUser"
          >
            Edit Users
          </button>
          {/* Modal Button For Notification*/}
          <button
            ref={sendNotification}
            type="button"
            className="btn btn-primary d-none"
            data-toggle="modal"
            data-target="#notification"
          >
            Push Notification
          </button>

          {/* Modal Button For Notification to all*/}
          <button
            ref={sendNotificationToALl}
            type="button"
            className="btn btn-primary d-none"
            data-toggle="modal"
            data-target="#notification_To_All"
          >
            Push Notification to all
          </button>

          {/* Modal Content For Commission*/}
          <div
            className="modal fade"
            id="editUser"
            tabIndex={-1}
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="exampleModalLabel">
                    Edit User Commission
                  </h5>
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true"></span>
                  </button>
                </div>
                <div className="modal-body">
                  <form
                    className="my-3"
                    onSubmit={(e) => {
                      handleSubmit(e);
                    }}
                  >
                    <div className="mb-3">
                      <label htmlFor="commission" className="form-label">
                        Commission
                      </label>
                      <input
                        type="number"
                        className="form-control"
                        id="commission"
                        name="commission"
                        onChange={onChangeUserComm}
                        value={editUserComm.commission}
                      />
                    </div>
                  </form>
                </div>
                <div className="modal-footer">
                  <button
                    type="button"
                    className="btn btn-secondary"
                    data-dismiss="modal"
                    ref={closeUM}
                  >
                    Close
                  </button>
                  <button
                    type="button"
                    className="btn btn-primary"
                    ref={clickU}
                    disabled={editUserComm.commission.length > 0 ? false : true}
                    onClick={async () => {
                      await updateUserComm(
                        closeUM,
                        uId.id,
                        editUserComm.commission
                      );
                      setEditUserComm({ commission: "" });
                    }}
                  >
                    Save changes
                  </button>
                </div>
              </div>
            </div>
          </div>

          {/* Modal Content For Notification */}
          <div
            className="modal fade"
            id="notification"
            tabIndex={-1}
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="exampleModalLabel">
                    For Notification
                  </h5>
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true"></span>
                  </button>
                </div>
                <div className="modal-body">
                  <form
                    className="my-3"
                    onSubmit={(e) => {
                      handleSubmitForNotification(e);
                    }}
                  >
                    <div className="mb-3">
                      <label htmlFor="title" className="form-label">
                        Title
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        id="title"
                        name="title"
                        onChange={onChangeNotification}
                        value={notification.title}
                      />
                    </div>
                    <div className="mb-3">
                      <label htmlFor="text" className="form-label">
                        Message
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        id="text"
                        name="text"
                        onChange={onChangeNotification}
                        value={notification.text}
                      />
                    </div>
                  </form>
                </div>
                <div className="modal-footer">
                  <button
                    type="button"
                    className="btn btn-secondary"
                    data-dismiss="modal"
                    ref={closeM}
                  >
                    Close
                  </button>
                  <button
                    type="button"
                    className="btn btn-primary"
                    ref={clickN}
                    disabled={notification.text.length > 0 ? false : true}
                    onClick={async () => {
                      await notificationApi(
                        closeM,
                        uId.id,
                        notification,
                        setNotification
                      );
                      setEditUserComm({ commission: "" });
                    }}
                  >
                    Send Notification
                  </button>
                </div>
              </div>
            </div>
          </div>

          {/* Modal Content For Notification to all*/}
          <div
            className="modal fade"
            id="notification_To_All"
            tabIndex={-1}
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="exampleModalLabel">
                    For Notification To ALL
                  </h5>
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true"></span>
                  </button>
                </div>
                <div className="modal-body">
                  <form
                    className="my-3"
                    onSubmit={(e) => {
                      handleSubmitNotification_To_All(e);
                    }}
                  >
                    <div className="mb-3">
                      <label htmlFor="title" className="form-label">
                        Title
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        id="title"
                        name="title"
                        onChange={onChangeNotification_To_All}
                        value={notification_To_All.title}
                      />
                    </div>
                    <div className="mb-3">
                      <label htmlFor="text" className="form-label">
                        Message
                      </label>
                      <input
                        type="text"
                        className="form-control"
                        id="text"
                        name="text"
                        onChange={onChangeNotification_To_All}
                        value={notification_To_All.text}
                      />
                    </div>
                  </form>
                </div>
                <div className="modal-footer">
                  <button
                    type="button"
                    className="btn btn-secondary"
                    data-dismiss="modal"
                    ref={closeAM}
                  >
                    Close
                  </button>
                  <button
                    type="button"
                    className="btn btn-primary"
                    ref={clickTN}
                    disabled={
                      notification_To_All.text.length > 0 ? false : true
                    }
                    onClick={async () => {
                      await notificationToAll(
                        closeAM,
                        notification_To_All,
                        setNotification_To_All
                      );
                    }}
                  >
                    Send Notification
                  </button>
                </div>
              </div>
            </div>
          </div>
          {/* modal for edit refer amount */}
          <div
            className="modal fade"
            id="editRefer"
            tabIndex={-1}
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="exampleModalLabel">
                    Edit Refer Amount
                  </h5>
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                    ref={closeR}
                  >
                    <span aria-hidden="true"></span>
                  </button>
                </div>
                <div className="modal-body">
                  <form
                    className="my-3"
                    onSubmit={(e) => {
                      referSubmit(e);
                    }}
                  >
                    <div className="mb-3">
                      <label htmlFor="refer" className="form-label">
                        Refer Amount
                      </label>
                      <input
                        type="number"
                        className="form-control"
                        id="refer"
                        name="refer"
                        onChange={onChangeRefer}
                        value={editRefer.refer}
                      />
                    </div>
                    <div className="modal-footer">
                      <button
                        type="button"
                        className="btn btn-secondary"
                        data-dismiss="modal"
                      >
                        Close
                      </button>
                      <button
                        type="submit"
                        className="btn btn-primary"
                        // disabled={editRefer.refer < 0 ? false : true}
                      >
                        Save changes
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>

          <div
            className="modal fade"
            id="DetailPage"
            tabIndex={-1}
            aria-labelledby="staticBackdropLabel"
            aria-hidden="true"
            data-bs-backdrop="static"
            data-bs-keyboard="false"
            aria-hidden="true"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="exampleModalLabel">
                    User Detail Amount
                  </h5>
                  <button
                    type="button"
                    className="close"
                    data-dismiss="modal"
                    aria-label="Close"
                    ref={clickDP}
                    onClick={() => {
                      dispatch(GetUserDetail([]));
                    }}
                  >
                    <span aria-hidden="true"></span>
                  </button>
                </div>
                <div className="modal-body">
                  {UserDetails.length >= 1 ? (
                    <form className="my-3">
                      {UserDetails.map((x, n) => {
                        return (
                          <div key={n}>
                            <div className="mb-3 user-select-none">
                              <label htmlFor="UserName" className="form-label">
                                UserName
                              </label>
                              <input
                                type="string"
                                className="form-control pe-none"
                                defaultValue={x.name}
                                disabled
                              />
                            </div>
                            <div className="mb-3 user-select-none">
                              <label htmlFor="mobile" className="form-label">
                                Mobile
                              </label>
                              <input
                                type="string"
                                className="form-control pe-none"
                                defaultValue={x.mobile}
                                disabled
                              />
                            </div>
                            <div className="mb-3 user-select-none">
                              <label htmlFor="plan" className="form-label">
                                Plan
                              </label>
                              <input
                                type="string"
                                className="form-control pe-none"
                                defaultValue={x.plan.toString()}
                                disabled
                              />
                            </div>
                            <div className="mb-3 user-select-none">
                              <label htmlFor="wallet" className="form-label">
                                Wallet
                              </label>
                              <input
                                type="string"
                                className="form-control pe-none"
                                defaultValue={x.wallet}
                                disabled
                              />
                            </div>
                            <div className="mb-3 user-select-none">
                              <label htmlFor="refer" className="form-label">
                                REFER NUMBER
                              </label>
                              <input
                                type="string"
                                className="form-control pe-none"
                                defaultValue={x.referCode}
                                disabled
                              />
                            </div>
                            <div className="mb-3 user-select-none">
                              <label htmlFor="tEarning" className="form-label">
                                Today Earning
                              </label>
                              <input
                                type="string"
                                className="form-control pe-none"
                                defaultValue={x.tEarning}
                                disabled
                              />
                            </div>
                            <div className="mb-3 user-select-none">
                              <label htmlFor="yEarning" className="form-label">
                                Yeterday Earning
                              </label>
                              <input
                                type="string"
                                className="form-control pe-none"
                                defaultValue={x.yEarning}
                                disabled
                              />
                            </div>
                            <div className="mb-3 user-select-none">
                              <label
                                htmlFor="PerAdsLimit"
                                className="form-label"
                              >
                                PerAdsLimit
                              </label>
                              <input
                                type="string"
                                className="form-control pe-none"
                                defaultValue={x.perDayAddLimit}
                                disabled
                              />
                            </div>
                            {/* <div className="mb-3 user-select-none">
                              <label htmlFor="yEarning" className="form-label">
                                Date of Joining
                              </label>
                              <input
                                type="string"
                                className="form-control pe-none"
                                // defaultValue={x.yEarning}
                                defaultValue={moment(x.date).format(
                                  "MMMM Do YYYY"
                                )}
                                disabled
                              />
                            </div> */}
                            {x.planExpireOn !== 0 ? (
                              <div className="mb-3 user-select-none">
                                <label
                                  htmlFor="planExpireOn"
                                  className="form-label"
                                >
                                  Plan Expire On
                                </label>
                                <input
                                  type="string"
                                  className="form-control pe-none"
                                  defaultValue={moment(
                                    parseInt(x.planExpireOn)
                                  ).format("MMMM Do YYYY")}
                                  disabled
                                />
                              </div>
                            ) : (
                              <></>
                            )}
                          </div>
                        );
                      })}
                      <div className="modal-footer">
                        <button
                          type="button"
                          className="btn btn-primary"
                          onClick={() => {
                            clickDP.current.click();
                            setToggleTransaction(true);
                          }}
                        >
                          Detail Transaction
                        </button>
                      </div>
                    </form>
                  ) : (
                    <div>Loding...</div>
                  )}
                </div>
              </div>
            </div>
          </div>

          {/* fILTERS */}
          <div className="row">
            <div className="col-md-12">
              <div className="card">
                <div className="header">
                  <h4 className="title">Filters</h4>
                </div>
                <div className="content">
                  <DIV className="row">
                    {/* <div className="col-md-3 col-xl-3 col-sm-6">
                    <div className="ml-xl-4 mt-3">
                      <input type="date" className="form-control" />
                    </div>
                  </div> */}

                    <div className="col-md-3 col-xl-3 col-sm-6">
                      <div className="ml-xl-4 mt-3">
                        <input
                          type="text"
                          onChange={(e) => setSearch(e.target.value)}
                          disabled={loader}
                          style={{
                            "border-radius": "20px",
                            padding: "2px",
                          }}
                          placeholder="search"
                        />
                      </div>
                    </div>

                    <div className="col-md-3 col-xl-3 col-sm-6">
                      <div className="ml-xl-4 mt-3">
                        <select
                          className="form-control"
                          value={condition.status}
                          disabled={loader}
                          onChange={(e) => OnstatusCodition(e)}
                        >
                          <option value="">Select Status</option>
                          <option>active</option>
                          <option>InActive</option>
                        </select>
                      </div>
                    </div>

                    <div className="col-md-3 col-xl-3 col-sm-6">
                      <div className="ml-xl-4 mt-3">
                        {packages.length > 0 ? (
                          <select
                            className="form-control"
                            value={condition.plan}
                            disabled={loader}
                            onChange={(e) => OnplanCodition(e)}
                          >
                            <option value="">Select Plan</option>
                            {packages.map((x, n) => (
                              <option key={n}>{x.plan}</option>
                            ))}
                          </select>
                        ) : (
                          <select className="form-control">
                            <option value="">Select Plan</option>
                          </select>
                        )}
                      </div>
                    </div>
                  </DIV>
                </div>
              </div>
            </div>
          </div>
          {loader ? (
            <>
              <Loader />
            </>
          ) : (
            <>
              {/* User Data */}
              <div className="content">
                <div className="container-fluid">
                  {/* USER CONTENT AND EDIT CONTENT */} {/* Pagination */}
                  <div className="row">
                    <div className="col-md-12">
                      <div className="card">
                        <div className="header">
                          <h4 className="title">Number of Users</h4>
                        </div>
                        <div
                          className={
                            users
                              ? "notUsers"
                              : "content table-responsive table-full-width"
                          }
                        >
                          {users[0] !== undefined ? (
                            <table className="table">
                              <thead>
                                <tr>
                                  <th className="text-center">No.</th>
                                  <th className="text-center">Name</th>
                                  <th className="text-center">Mobile</th>
                                  <th className="text-center">Plan</th>
                                  {/* <th className="text-center">PerAdsLimit</th> */}
                                  <th className="text-center">Commission</th>
                                  {/* <th className="text-center">Refer Number</th> */}
                                  <th className="text-center">Date of Orgin</th>
                                  <th className="text-center">Status</th>
                                  <th className="text-center">Detail</th>
                                  <th className="text-center">edit</th>
                                  <th className="text-center">Notification</th>
                                </tr>
                              </thead>

                              <tbody>
                                {filterList
                                  ?.slice(
                                    parseInt(pageNo.page * pageNo.perPage),
                                    parseInt(pageNo.page * pageNo.perPage) +
                                      parseInt(pageNo.perPage)
                                  )
                                  .map((x, index) => {
                                    return (
                                      <tr key={index}>
                                        <td className="text-center">
                                          {index + 1}
                                        </td>
                                        <td className="text-center">
                                          {x.name}
                                        </td>
                                        <td className="text-center">
                                          {x.mobile}
                                        </td>
                                        <td className="text-center">
                                          {x.plan}
                                        </td>
                                        {/* <td className="text-center">
                                          {x.perDayAddLimit}
                                        </td> */}
                                        <td className="text-center">
                                          {x.commission}
                                        </td>
                                        {/* <td className="text-center">
                                          {x.referCode}
                                        </td> */}
                                        <td className="text-center">
                                          {moment(x.date).format(
                                            "MMMM Do YYYY"
                                          )}
                                        </td>
                                        <td className="text-center">
                                          {x.status}
                                        </td>
                                        <td className="text-center">
                                          <i
                                            type="button"
                                            className="btn btn-primary"
                                            data-toggle="modal"
                                            data-target="#DetailPage"
                                            data-bs-target="#staticBackdrop"
                                            className="fa fa-eye"
                                            onClick={() => {
                                              UserDetail(x._id);
                                            }}
                                          />
                                        </td>
                                        <td className="text-center">
                                          <i
                                            className="fa fa-pencil"
                                            onClick={() => {
                                              updateCommission(x._id);
                                            }}
                                          />
                                        </td>
                                        <td className="text-center">
                                          <Button
                                            onClick={() => {
                                              sendNotifications(x._id);
                                            }}
                                          >
                                            <i
                                              className="fa fa-paper-plane"
                                              aria-hidden="true"
                                            ></i>
                                            Click here
                                          </Button>
                                        </td>
                                      </tr>
                                    );
                                  })}
                              </tbody>
                            </table>
                          ) : (
                            <h3>Users Doesn't Exist</h3>
                          )}
                        </div>
                        <PaginationComponent
                          pageNo={pageNo.page}
                          handlePage={handlePage}
                          disabled={
                            (pageNo.page + 1) * pageNo.perPage >= users.length
                          }
                        />
                        {/* <Pagination>
                          <button
                            type="button"
                            className="btn btn-primary"
                            disabled={pageNo.page <= 1}
                            onClick={(e) => handlePage(e, -1)}
                          >
                            prev
                          </button>
                          <p>Page-{pageNo.page}</p>
                          <button
                            type="button"
                            className="btn btn-primary"
                            disabled={
                              pageNo.page * pageNo.perPage >= pageNo.documents
                            }
                            onClick={(e) => handlePage(e, +1)}
                          >
                            next
                          </button>
                        </Pagination> */}
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-md-12">
                      <PushButton>
                        <button
                          type="button"
                          className="btn"
                          onClick={async (e) => {
                            e.preventDefault();
                            sendNotificationToALl.current.click();
                          }}
                        >
                          <i
                            className="fa fa-paper-plane"
                            aria-hidden="true"
                          ></i>
                          Push Notification To All
                        </button>
                        <ChangeReferAmt
                          type="button"
                          className="btn"
                          data-toggle="modal"
                          data-target="#editRefer"
                          // onClick={setEditRefer({refer:`${refer.referamt}`})}
                        >
                          {`Edit Refer - ${refer.referamt}`}
                        </ChangeReferAmt>
                      </PushButton>
                    </div>
                  </div>
                </div>
              </div>
            </>
          )}
        </>
      )}
    </>
  );
};

export default UserData;

const Header = styled.div`
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  .b-0 {
  }
  .b-0:hover {
    color: white;
    background-color: #7a7ae4;
    border-radius: 50%;
  }
`;
const Button = styled.button`
  border: none;
  background-color: white;
  /* border-radius: ; */
`;
const DIV = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* border-radius: ; */
`;

const PushButton = styled.div`
  display: flex;
  justify-content: center;
  button {
    background-color: #efeff2;
    color: black;
    border: none;
    padding: 20px;
    border-radius: 55%;
  }
`;

const ChangeReferAmt = styled.button`
  margin-left: 5px;
  background-color: #efeff2;
  color: black;
  border: none;
  padding: 20px;
  border-radius: 55%;
`;
