import React, { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import styled from "styled-components";
import useAdminContexts from "../Context/AdminContext";
import { fetchBaners } from "../ReduxProvider/CounterSlice";
import Loader from "./partials/Loader";

const Banner = () => {
  // const formImage = useRef(null)
  const closeBnRef = useRef(null);
  const [imgs, setImgs] = useState("");
  const { addBanner, fetchBanners, bannerDelete, loader } = useAdminContexts();
  const banners = useSelector(fetchBaners);

  const add = (e) => {
    // console.log(e.target.files.length <= 0);
    let val = e.target.files;
    if (val.length <= 0) {
      setImgs("");
    } else {
      setImgs(e.target.files);
    }
  };

  const formData = async (e) => {
    e.preventDefault();
    addBanner(imgs, closeBnRef, setImgs);
  };

  useEffect(() => {
    fetchBanners();
    // eslint-disable-next-line
  }, []);
  console.log(imgs);
  return (
    <>
      {/* Modal Content*/}
      <div
        className="modal fade"
        id="CreateBanner"
        tabIndex={-1}
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        {/* Modal Form */}
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Add Banner
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true" ref={closeBnRef}>
                  
                </span>
              </button>
            </div>
            <div className="modal-body">
              <AddImage className="my-3" onSubmit={formData}>
                <label htmlFor="files">Select files:</label>
                <input
                  type="file"
                  accept=".png, .jpg,"
                  id="img"
                  name="img"
                  required
                  onChange={add}
                  multiple
                />
                {/* <button type="button" onClick={formData}>
                  Add Image
                </button> */}
              </AddImage>
              <div className="modal-footer">
                {/* <button
                type="button"
                className="btn btn-secondary"
                data-dismiss="modal"
              >
                Close
              </button> */}
                <button
                  type="button"
                  className="btn btn-primary"
                  disabled={imgs === "" ? true : false}
                  onClick={formData}
                >
                  Create Banner
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>

      {loader ? (
        <>
          <Loader />
        </>
      ) : (
        <div className="content">
          <div className="container-fluid">
            <div className="col-md-12">
              <div className="">
                <ModalHead className="header">
                  <ModalButton className="d-flex flex-column justify-content-center">
                    {/* Button trigger modal */}
                    <button
                      type="button"
                      className="btn btn-primary"
                      data-toggle="modal"
                      data-target="#CreateBanner"
                    >
                      Add Banner
                    </button>
                  </ModalButton>
                </ModalHead>
              </div>
            </div>
            <Baner className="row">
              {banners.map((x, index) => {
                return (
                  <div className="col-sm-3" key={index}>
                    <div className="card">
                      <Image>
                        <img src={`${x.bannerImage}`} alt="not available" />
                        <I>
                          <i
                            className="fa fa-trash"
                            onClick={() => bannerDelete(x._id, x.bannerImage)}
                          />
                        </I>
                      </Image>
                    </div>
                  </div>
                );
              })}
              {/* <div className="col-sm-3">
                <div className="card">
                  <AddImage className="">
                    <label htmlFor="files">Select files:</label>
                    <input
                      type="file"
                      accept=".png, .jpg,"
                      id="img"
                      name="img"
                      onChange={add}
                      multiple
                    />
                    <button type="button" onClick={formData}>
                      Add Image
                    </button>
                  </AddImage>
                </div>
              </div> */}
            </Baner>
          </div>
        </div>
      )}
    </>
  );
};

export default Banner;

const Baner = styled.div`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
`;

const I = styled.i`
  display: none;
  width: 100%;
  height: 100%;
  i {
    position: absolute;
    top: 31%;
    font-size: 50px;
    left: 44%;
    width: fit-content;
  }
`;
const Image = styled.div`
  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  &:hover ${I} {
    display: block;
  }
`;

const AddImage = styled.form`
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;

  input {
    margin: 10px auto;
  }
  button {
    background: #567ac8;
    border: none;
    color: white;
  }
`;

const ModalButton = styled.div`
  display: flex;
  /* width: 15%; */
  flex-direction: column;
  justify-content: center;
  button &:hover {
    background-color: #3472F7,
    color: #ffff
  }
  button{
    border-radius: 50px
  }
`;

const ModalHead = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
`;
