import React, { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import styled from "styled-components";
import useAdminContexts from "../Context/AdminContext";
import { fetchVideoss } from "../ReduxProvider/CounterSlice";
import Loader from "./partials/Loader";

const VideoComponent = () => {
  // const formImage = useRef(null)
  const closeVideoRef = useRef(null);
  const [imgs, setImgs] = useState("");
  const { addVideos, fetchAllVideos, deleteVideos, loader } =
    useAdminContexts();
  const fetchVideos = useSelector(fetchVideoss);

  const add = (e) => {
    let val = e.target.files;
    if (val.length <= 0) {
      setImgs("");
    } else {
      setImgs(e.target.files);
    }
  };

  const formData = async (e) => {
    e.preventDefault();
    if (imgs) {
      addVideos(imgs, closeVideoRef, setImgs);
    } else {
      alert("Please Upload Video For Submit");
    }
  };

  useEffect(() => {
    fetchAllVideos();
    // eslint-disable-next-line
  }, []);

  console.log(imgs);
  return (
    <>
      {/* Modal Content*/}
      <div
        className="modal fade"
        id="CreateVideo"
        tabIndex={-1}
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        {/* Modal Form */}
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Add Video
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true" ref={closeVideoRef}>
                  
                </span>
              </button>
            </div>
            <div className="modal-body">
              <AddImage className="my-3">
                <label htmlFor="files">Select files:</label>
                <input
                  type="file"
                  accept=".mp4,"
                  id="video"
                  name="video"
                  required
                  onChange={add}
                  multiple
                />
                {/* <button type="button" onClick={formData}>
                  Add Image
                </button> */}
              </AddImage>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-primary"
                  disabled={imgs === "" ? true : false}
                  onClick={(e) => {
                    formData(e);
                  }}
                >
                  Save Video
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      {loader ? (
        <>
          <Loader />
        </>
      ) : (
        <div className="content">
          <div className="container-fluid">
            <div className="col-md-12 mb-3">
              <div>
                <ModalHead
                  className="header mb-3"
                  style={{ marginBottom: "30px" }}
                >
                  <ModalButton className="d-flex flex-column justify-content-center">
                    {/* Button trigger modal */}
                    <button
                      type="button"
                      className="btn btn-primary"
                      data-toggle="modal"
                      data-target="#CreateVideo"
                    >
                      Add Video
                    </button>
                  </ModalButton>
                </ModalHead>
              </div>
            </div>
            <Baner className="row">
              {fetchVideos.map((x, index) => {
                return (
                  <div className="col-4" key={index}>
                    <div className="card">
                      <Image>
                        <video
                          controls
                          src={x.AdminVideo}
                          type="video/mp4"
                          alt="Your browser does not support the video tag."
                        />
                        {/* <source src={x.AdminVideo} type="video/mp4" /> */}
                        {/* Your browser does not support the video tag.
                      </video> */}
                        <I>
                          <i
                            className="fa fa-trash"
                            onClick={(e) => {
                              deleteVideos(x._id, x.AdminVideo);
                            }}
                          />
                        </I>
                      </Image>
                    </div>
                  </div>
                );
              })}
              {/* <div className="col-sm-4">
                <div className="card">
                  <AddImage className="">
                    <label htmlFor="files">Select files:</label>
                    <input
                      type="file"
                      accept=".mp4,"
                      id="video"
                      name="video"
                      onChange={add}
                      multiple
                    />
                    <button type="button">Add Video</button>
                  </AddImage>
                </div>
              </div> */}
            </Baner>
          </div>
        </div>
      )}
    </>
  );
};

export default VideoComponent;

const Baner = styled.div`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
  /* .col-3 {
    box-shadow: 0 1px 2px rgb(0 0 0 / 5%), 0 0 0 1px rgb(63 63 68 / 10%);
    background-color: #ffffff;
  } */
  /* .card{
    box-shadow: 0;
  } */
`;

const I = styled.i`
  display: none;
  width: 100%;
  height: 100%;
  i {
    position: absolute;
    top: 31%;
    font-size: 50px;
    left: 44%;
    width: fit-content;
  }
`;
const Image = styled.div`
  position: relative;
  video {
    /* width: 100%; */
    width: 320px;
    max-height: 180px;
    /* object-fit: contain; */
  }
  &:hover ${I} {
    display: block;
  }
`;

const AddImage = styled.form`
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  input {
    margin: 10px auto;
    /* width: 70%; */
  }
  button {
    background: #567ac8;
    border: none;
    color: white;
  }
`;

const ModalButton = styled.div`
  display: flex;
  /* width: 15%; */
  flex-direction: column;
  justify-content: center;
  button &:hover {
    background-color: #3472F7,
    color: #ffff
  }
  button{
    border-radius: 50px
  }
`;

const ModalHead = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: flex-end;
`;
